@import '../../styles/variables.scss';
@import '../../styles/_mixins.scss';

.carEdit {
  padding: 28px;
  &__title {
    margin: 0;
    margin-bottom: 28px;
    font-weight: normal;
    font-size: 29px;
    line-height: 33px;
    color: $main-accent;
  }
  &__content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 20px;
  }

  &__preview {
    @include whiteBox;
    padding: 20px 15px;
  }
}

.customization {
  @include whiteBox;
  padding: 20px 15px;
  &__subtitle {
    margin: 0;
    margin-bottom: 10px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 17.5px;
    line-height: 21px;
    color:$main-accent;
  }
  &__layout {
    display: flex;
  }
  &__col {
    flex-basis: 50%;
  }
}
